@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after{content:'';content:none}q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}body{font-family:'Roboto', sans-serif;font-size:14px;font-weight:400}

.App{display:flex;justify-content:center}.App__container{display:flex;flex-direction:column;min-height:100vh;width:75%}

.Header{display:flex;flex-shrink:0;padding-top:48px;padding-bottom:48px}

.Footer{display:flex;justify-content:space-between;align-items:center;flex-shrink:0;padding-top:30px;padding-bottom:30px;font-size:14px;font-weight:400}.Footer__text{font-size:14px;font-weight:400}.links__item{text-decoration:none;text-transform:uppercase;font-weight:700;letter-spacing:2px;color:#000;margin-right:30px;transition:color 0.3s}.links__item:hover{color:#800505}.links__item:last-child{margin-right:0}

.container{display:flex;justify-content:center;align-items:center;flex-grow:1}.TokenForm{display:flex;flex-direction:column;align-items:center;color:#000}.TokenForm__title{font-size:64px;font-weight:500;margin-bottom:3px}.TokenForm__subtitle{font-weight:500;margin-bottom:24px}.TokenForm__form{display:flex;flex-direction:column;justify-content:flex-start}.form__item{display:flex;flex-direction:column;margin-bottom:16px}.form__item__name{font-size:12px;color:#727272;margin-bottom:5px}.form__item__label{cursor:pointer}.form__item__select{width:-webkit-max-content;width:max-content;border:none}.form__item__checkbox{position:absolute;opacity:0;margin-right:10px;cursor:pointer}.form__item__text{position:relative;left:25px;font-weight:500}.form__item__checkbox:checked ~ .form__item__checkmark{background-color:#c50000;border:2px solid #bbb}.form__item__checkbox:checked ~ .form__item__checkmark:after{display:block}.form__item__checkmark{position:absolute;height:13px;width:13px;background-color:#fff;border:1px solid #bbb}.form__item__checkmark:after{content:"";position:absolute;display:none}.form__button{max-width:130px;min-height:36px;font-size:16px;font-weight:900;color:#fff;text-transform:uppercase;letter-spacing:0.04em;background-color:#c50000;border:none;border-radius:48px;cursor:pointer;transition:background-color 0.3s}.form__button:hover{background-color:#800505}.form__supply{position:relative;margin-bottom:60px}

.TokenFormSubmitted__item{font-size:18px;font-weight:400;margin-bottom:10px}.TokenFormSubmitted__item:last-child{margin-bottom:0}

.TokenFormInput{display:flex;flex-direction:column;margin-bottom:16px}.TokenFormInput__name{font-size:12px;color:#727272;margin-bottom:5px}.TokenFormInput__input{box-sizing:border-box;min-width:320px;max-height:30px;box-sizing:border-box;border:1px solid #bbb;border-radius:8px;padding:8px}

.TokenFormRadio__container{display:flex;flex-direction:row}.TokenFormRadio__item{display:flex;flex-direction:column;margin-bottom:16px}.TokenFormRadio__name{font-size:12px;color:#727272;margin-bottom:5px}.TokenFormRadio__input{opacity:0;position:fixed}.TokenFormRadio__input:focus+.TokenFormRadio__label{background-color:#c50000;color:#fff}.TokenFormRadio__input:checked+.TokenFormRadio__label{background-color:#c50000;color:#fff}.TokenFormRadio__label{padding:7px 12px;color:#000;font-weight:500;cursor:pointer;border:1px solid #bbb;border-radius:28px;margin-right:10px;transition:all 0.3s}.TokenFormRadio__label:hover{background-color:#800505;color:#fff}.TokenFormRadio__label:last-child{margin-right:0}

